import './styles.css';
import { useForm } from '@formspree/react';
import { toast, ToastContainer } from 'react-toastify';
import { useEffect, useState } from 'react';
import 'react-toastify/dist/ReactToastify.css';

export function Form() {
    const [state, handleSubmit] = useForm('xknkpqry')
    const [formData, setFormData] = useState({
        email: '',
        message: ''
    });
    const [isSubmitting, setIsSubmitting] = useState(false);
    
    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData(prev => ({
            ...prev,
            [name]: value
        }));
    };
    
    const validateForm = () => {
        // Simple email validation
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        const isEmailValid = emailRegex.test(formData.email);
        const isMessageValid = formData.message.trim().length > 0;
        
        return isEmailValid && isMessageValid;
    };
    
    const onSubmit = async (e) => {
        e.preventDefault();
        if (!validateForm()) {
            toast.error('Please fill in all fields correctly', {
                position: toast.POSITION.BOTTOM_LEFT
            });
            return;
        }
        
        setIsSubmitting(true);
        try {
            await handleSubmit(formData);
        } catch (error) {
            console.error('Form submission error:', error);
            toast.error('Failed to send message. Please try again later.', {
                position: toast.POSITION.BOTTOM_LEFT
            });
        } finally {
            setIsSubmitting(false);
        }
    };
    useEffect(() => {
        if (state.succeeded) {
            toast.success('Email successfully sent!', {
                position: toast.POSITION.BOTTOM_LEFT,
                pauseOnFocusLoss: false,
                closeOnClick: true,
                hideProgressBar: false,
                toastId: 'succeeded',
            });
            // Reset form on success
            setFormData({ email: '', message: '' });
        } else if (state.errors && state.errors.length > 0) {
            const errorMessage = state.errors[0]?.message || 'Failed to send message. Please try again.';
            toast.error(errorMessage, {
                position: toast.POSITION.BOTTOM_LEFT,
                pauseOnFocusLoss: false,
                closeOnClick: true,
                hideProgressBar: false,
                toastId: 'error',
            });
        }
    }, [state.succeeded, state.errors]);
    if (state.succeeded) {
        return (
            <div className="form-success">
                <h3>Thanks for getting in touch!</h3>
                <button
                    onClick={() => {
                        window.scrollTo({ top: 0, behavior: 'smooth' })
                    }}
                >
                    Back to the top
                </button>
                <ToastContainer />
            </div>
        )
    }
    return (
        <div className="form-container">
            <h2>Get in touch using the form</h2>
            <form onSubmit={onSubmit}>
                <div className="form-group">
                    <input
                        placeholder="Email"
                        id="email"
                        type="email"
                        name="email"
                        value={formData.email}
                        onChange={handleChange}
                        required
                        className={`form-control ${formData.email && !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email) ? 'error' : ''}`}
                    />
                    {formData.email && !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email) && (
                        <div className="error-message">Please enter a valid email address</div>
                    )}
                </div>
                
                <div className="form-group">
                    <textarea
                        placeholder="Send a message to get started."
                        id="message"
                        name="message"
                        value={formData.message}
                        onChange={handleChange}
                        required
                        className={`form-control ${formData.message && formData.message.trim().length === 0 ? 'error' : ''}`}
                    />
                    {formData.message && formData.message.trim().length === 0 && (
                        <div className="error-message">Message cannot be empty</div>
                    )}
                </div>
                
                <button
                    type="submit"
                    disabled={isSubmitting || !validateForm()}
                    className={isSubmitting ? 'submitting' : ''}
                >
                    {isSubmitting ? (
                        <>
                            <span className="spinner"></span>
                            Sending...
                        </>
                    ) : 'Submit'}
                </button>
            </form>
            <ToastContainer />
        </div>
    )
}